.product-list-container {
  display: flex;
  flex-wrap: wrap;
  padding-top: 10px;
}

.product-card {
  flex-basis: 300px;
  width: 250px;
  height: 250px;
  padding: 2.5px;
  margin: auto;
}

.product-card h4, .product-card p {
   text-align: center;
   padding: 2.5px;
}

.product-card img, .product-card button {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 50%;
}

.cart-header {
  padding: 10px;
  text-align: center;
}

.cart-container {
  text-align: center;
  padding-top: 10px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.cart-summary {
  flex-basis: 25%;
  padding: 10px;
  margin: 10px;
  text-align: center;
  background-color: #534D56;
  color: #f8f1ff;
}

.cart-items {
  flex-basis: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.cart-item {
  padding: 10px;
}

.subtotal {
  padding: 10px;
  font-weight: 700;
}

@media screen and (min-width: 768px) { 
  .cart-items {
    flex-basis: 75%;
    padding: 10px;
    display: flex;
    flex-wrap: wrap;
  }
}
.home-banner {
    padding: 75px 0 0 0;
    text-align: center;
    margin: auto;
    background-image: url('https://images.pexels.com/photos/414171/pexels-photo-414171.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940');
    background-size: cover;
    min-height: 300px;
    color: white;
    text-shadow: 1px 1px 2px black;
}

.store-btn {
    margin: auto;
}

.store-btn:hover {
    background-color: seagreen;
}

.featured-items {
    display: flex;
    flex-wrap: wrap
}

.featured-image {
    background-image: url('https://images.pexels.com/photos/35435/pexels-photo.jpg?auto=compress&cs=tinysrgb&dpr=2&w=400');
    width: 100%;
    padding-top: 400px;
    object-fit: cover;
}

.featured-content {
    min-height: 400px;
    width: 100%;
    background-color: #decdf5;
    text-align: center;
    padding: 150px 50px;
}

@media screen and (min-width: 768px) { 
    .featured-image {
    width: 50%;
    object-fit: contain;   
}

.featured-content {
    width: 50%;
} 


}
.footer-container {
    flex-shrink: 0;
    background-color: #534D56;
    color: #f8f1ff;
    display: flex;
    flex-wrap: wrap;
    padding: 10px;
}

.footer-column {
    flex-basis: 100%;
    padding-top: 10px;
    text-align: center;
}

@media screen and (min-width: 768px) { 
    .footer-column {
        flex-basis: 33%;
        text-align: left;
    }
}

.contact-container {
  text-align: center;
  padding: 25px;
}

input, textarea {
  display: inline-block;
  margin: 10px;
  padding: 5px;
  min-width: 90%;
}

@media screen and (min-width: 768px) { 
  input, textarea {
    display: inline-block;
    margin: 10px;
    padding: 5px;
    min-width: 50%;
  }

}
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
body {
  font-family: 'Josefin Sans', sans-serif;
  background-color: #f8f1ff;
  color: #534D56;
}


a:hover {
  text-decoration: none;
}

.App {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.content {
  flex: 1 0 auto;
}

.btn {
  background-color: #1b998b;
  border: none;
  color: #f8f1ff;
  padding: 10px 22px;
  text-align: center;
  text-decoration: none;
  font-size: 1.5em;
}

.btn:hover {
  background-color: seagreen;
}

.btn-small {
  background-color: #1b998b;
  border: none;
  color: #f8f1ff;
  padding: 5px 25px;
  text-align: center;
  text-decoration: none;
  font-size: 1.1em;
}

.btn-small:hover {
  background-color: seagreen;

}
