.contact-container {
  text-align: center;
  padding: 25px;
}

input, textarea {
  display: inline-block;
  margin: 10px;
  padding: 5px;
  min-width: 90%;
}

@media screen and (min-width: 768px) { 
  input, textarea {
    display: inline-block;
    margin: 10px;
    padding: 5px;
    min-width: 50%;
  }

}